body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul, li{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.nav-link {
    font-size: 20px !important;
}


.nav-link.active {
    color: #ffe300 !important;
}

.navbar-brand {
    font-size: 25px;
}

.wrap-random-planet, .short-details {
    border-radius: 5px;
}

.radius-img {
    border-radius: 5px;
}

.list-group-item-action.active {
    background: #222 !important;
    border-color: #444;
}


.short-details-info{
    width: 100%;
}


.short-details-info h2{
    font-size: 25px;
}

.list-group-item{
    cursor: pointer;
}


.list-info li {
    position: relative;
    padding: 5px 10px;
}


.list-info li:before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    background: #505050;
    top: 0;
    left: 0;
}


.error-indicator {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.error-indicator .boom {
    font-size: 25px;
}


@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.lds-css {
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.lds-double-ring {
    position: relative;
}

.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #1c1e22 transparent #1c1e22 transparent;
    -webkit-animation: lds-double-ring 0.7s linear infinite;
    animation: lds-double-ring 0.7s linear infinite;
}

.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #ffe300 transparent #ffe300;
    -webkit-animation: lds-double-ring_reverse 0.7s linear infinite;
    animation: lds-double-ring_reverse 0.7s linear infinite;
}

.lds-double-ring {
    width: 52px !important;
    height: 52px !important;
    -webkit-transform: translate(-26px, -26px) scale(0.26) translate(26px, 26px);
    transform: translate(-26px, -26px) scale(0.26) translate(26px, 26px);
}
